import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { SubscriptButton as Button } from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
import { IconButtonShortcut } from '@ali/4ever-component';

;
import { toggleSub } from '../actions';
import { SHORTCUT } from '../utils';
;

export interface SubButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export default function SubButton(props: SubButtonProps) {
  const { controller, locale, ...rest } = props;
  const isActive = controller.query('isSub');
  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = isCollapsedAtEdge
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInHr')
    || controller.query('isSelectionInMention')
    || controller.query('isSelectionInLink')
    || controller.query('isSelectionInListSymbol')
    || controller.query('isSelectionInLinkCard');

  const handleClick = React.useCallback(() => controller.run('onAction', toggleSub()), [controller]);

  const title =  locale?.subButtonTitle || '';
  const shortCut = SHORTCUT.sub;
  const description =  `Markdown: ~${locale?.text || 'Text'}~ + ${locale?.space || 'Space'}`;

  return (
    <Button
      {...rest}
      shortcutText={SHORTCUT.sub}
      testid="bi-toolbar-sub"
      active={isActive}
      disabled={!!isDisabled}
      onClick={handleClick}
      role={PluginRoles.vertAlignSub}
      tooltip={<IconButtonShortcut title={title} shortcut={shortCut} description={description} />}
    />
  );
}
